<template>
  <transition name="show">
    <div id="login" class="container-fluid p-3 px-lg-5 py-lg-4">
      <div class="logo row text-center">
        <img
          src="@/assets/logo-s2-white.png"
          alt="logo"
          class="mx-auto my-2 my-md-5"
          v-on:click="backToHome()"
        />
      </div>
      <div class="login__form row px-2 py-3 mx-auto">
        <div class="login__form--first col-12">
          <span>Sign in</span>
          <span>with your ICO Account</span>
        </div>
        <div class="login__form--second col-12">
          <input
            type="text"
            name="username"
            placeholder="Your Email"
            id="username"
            v-on:keyup.enter="sendLoginRequest()"
          />
          <input
            type="password"
            name="password"
            placeholder="Password"
            id="password"
            v-on:keyup.enter="sendLoginRequest()"
          />
        </div>
        <div class="login__form--third col-12">
          <button v-on:click="sendLoginRequest()">Login</button>
          <span>
            <a href="/register">Don't have an account? Register</a>
          </span>
        </div>
        <div class="login__form--fourth col-12">
          <button class="getDemoAccount" v-on:click="showAccountDemo()">
            Get demo account?
          </button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  methods: {
    backToHome() {
      location.replace("/");
    },
    sendLoginRequest() {
      let username = document.getElementById("username").value;
      let password = document.getElementById("password").value;

      if (this.$session.has(username)) {
        if (this.$session.get(username) === password) {
          this.$session.set("isLogged", username);
          location.href = "/home";
        } else {
          this.$swal({
            title: "Login Failed",
            text: "Your email or password is incorrect!",
            icon: "error",
          });
        }
      } else {
        this.$swal({
          title: "Login Failed",
          text: "This account does not exist",
          icon: "error",
        });
      }
    },
    showAccountDemo() {
      this.$swal.fire({
        customClass: {
          container: "container-class",
          popup: "popup-class",
          header: "header-class",
          title: "title-class",
          closeButton: "close-button-class",
          content: "content-class",
          input: "input-class",
          actions: "actions-class",
          footer: "footer-class",
        },
      });
      this.$swal({
        title: "This is not a feature!",
        text: "Username: admin, Password: admin",
        icon: "info",
      });
      document.querySelector(".swal2-title").style.color = "#f42f54";
      document.querySelector(".swal2-confirm").innerHTML = "Got it";
    },
  },
  mounted() {
    if( this.$session.has('isLogged')) {
      location.href ="/home";
    }
  }
};
</script>

<style lang="scss" scoped>
#login::-webkit-scrollbar {
  display: none;
}
#login {
  max-width: 100vw;
  min-height: 100vh;
  background-color: #122272;
  .logo {
    img {
      cursor: pointer;
    }
  }
  .login__form {
    width: 400px;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
    .login__form--first,
    .login__form--second,
    .login__form--third,
    .login__form--fourth {
      margin-bottom: 25px;
    }

    .login__form--first {
      span {
        display: block;
        text-align: center;
        &:first-child {
          color: #1940b0;
          font-size: 2.25rem;
          font-weight: 300;
          margin-bottom: 10px;
        }
        &:last-child {
          color: #415076;
          font-size: 1.2rem;
        }
      }
    }
    .login__form--second {
      input {
        width: 100%;
        padding: 10px 15px;
        border: 1px solid rgb(156, 156, 156);
        border-radius: 10px;
        outline: none;
        &:first-child {
          margin-bottom: 20px;
        }
        &::placeholder {
          color: #818181;
        }
      }
    }
    .login__form--third,
    .login__form--fourth {
      text-align: center;
      button {
        padding: 7px 30px;
        border: 1px solid #16a3fe;
        border-radius: 10px;
        background-color: #16a3fe;
        outline: none;
        color: #fff;
        margin-bottom: 10px;
        transition: all 0.3s;
        &:hover {
          border-color: #f42f54;
          background-color: #f42f54;
        }
      }
      span {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .login__form {
    width: 100% !important;
  }
}
.show-enter,
.show-leave-to {
  opacity: 0;
}
.show-enter-to {
  opacity: 1;
}
.show-enter-active {
  transition: all 0.8s;
}
.show-leave-active {
  transition: all 0.3s;
}
</style>
