<template>
  <div id="preload">
    <div class="preload--center"></div>
    <img src="@/assets/logo-s2-white.png" alt="logo" />
  </div>
</template>

<script>
export default {
  props: ["WOW"],
  methods: {
    resetAnimation() {
      this.WOW.init();
    },
    removePreloader(callback) {
      window.onload = function() {
        document.getElementById("preload").style.display = "none";
        callback();
      };
    },
  },
  mounted() {
    this.removePreloader(this.resetAnimation);
  },
};
</script>

<style lang="scss" scoped>
#preload {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b2747;
  z-index: 99;
  .preload--center {
    position: relative;
    width: 200px;
    height: 200px;
    border: 2px solid rgb(180, 179, 179);
    border-right-color: #fff;
    border-radius: 50%;
    animation: clockwise 1.5s infinite linear;
  }
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 100px;
    transform: translate(-50%, -50%);
  }
}

@keyframes clockwise {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>

