<template>
  <header
    id="header"
    class="row p-3 px-lg-5 py-lg-4 wow fadeInDown"
    data-wow-duration="1s"
  >
    <div class="logo col-10 col-md-12 col-lg-3 px-3 p-lg-0">
      <img
        src="@/assets/logo-s2-white.png"
        alt="logo"
        class="mx-md-auto mx-lg-0"
        @click="reload()"
      />
    </div>
    <ul class="menu p-0 col-md-12 col-lg-9 d-none d-md-block ">
      <li class="ele">
        home<i class="fas fa-angle-down"></i>
        <ul class="dropdown">
          <li v-for="(ele, index) in home" :key="index">
            <span> {{ ele }} </span>
          </li>
        </ul>
      </li>
      <li class="ele">
        page<i class="fas fa-angle-down"></i>
        <ul class="dropdown">
          <li v-for="(ele, index) in page" v-bind:key="index">{{ ele }}</li>
        </ul>
      </li>
      <li class="ele">blog & Misc</li>
      <li class="ele">elements</li>
      <li class="ele">blocks</li>
      <li class="ele" @click="forwardLoginForm()">
        {{ getUsername() }}
      </li>
    </ul>
    <i
      class="fas fa-bars d-block d-md-none col-2 text-right"
      style="cursor: pointer;"
      @click="isShow.value = !isShow.value"
    ></i>
  </header>
</template>

<script>
export default {
  props: ["isShow", "isShowLoginForm", "isLoginSuccessfully"],
  data() {
    return {
      home: [
        "Azelea Multi",
        "Gentian Dark",
        "Gentian Dark",
        "Gentian Dark",
        "Aruze Pro",
        "Aruze Pro",
        "Aruze Pro",
        " Azelea Dar",
        "Azelea Mul",
        "Gentian Da",
        "Gentian Dark",
        "Aruze Pro",
        "Aruze Pro",
        " Azelea Dar",
        "Azelea Mul",
        "Gentian Da",
      ],
      page: [
        "About Base",
        "About Linum",
        "About Cyanus",
        "Our team",
        "Wallets - v1",
        "Wallets - v2",
        "About Base",
        "About Linum",
        "About Cyanus",
        "Our team",
        "Wallets - v1",
        "Wallets - v2",
      ],
    };
  },
  methods: {
    changeBackground() {
      // this function to change background of the header when user scroll
      let header = document.getElementById("header");
      let changeable = true;
      window.onscroll = function() {
        let position = window.scrollY;
        if (position === 0) {
          header.style.backgroundColor = "transparent";
          changeable = true;
        } else if (position !== 0 && changeable) {
          header.style.backgroundColor = "rgba(10, 18, 39, 0.99)";
          changeable = false;
        }
      };
    },
    reload() {
      location.href = "/";
    },
    forwardLoginForm() {
      location.href = "/login";
    },
    getUsername() {
      if (this.$session.has("isLogged")) {
        let result = this.$session.get("isLogged");
        if (result.length > 8) {
          return result.substring(0,8) + "...";
        }
        return result;
      }
      return "Login";
    },
  },
  mounted() {
    this.changeBackground();
  },
};
</script>

<style lang="scss" scoped>
header {
  display: flex;
  align-items: center;
  background-color: transparent;
  transition: all 0.5s;
  position: sticky;
  top: 0;
  z-index: 10;
  .logo {
    img {
      display: block;
      cursor: pointer;
    }
  }
  .menu {
    text-align: right;
    .ele {
      position: relative;
      display: inline-block;
      text-transform: capitalize;
      color: #fff;
      letter-spacing: 1px;
      transition: all 0.3s;
      &:first-child,
      &:nth-child(2) {
        i {
          transition: all 0.3s;
          font-size: 12px;
          margin-left: 5px;
        }
      }

      &:not(:first-child) {
        margin-left: 2rem;
      }
      &:last-child {
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 550;
        padding: 10px 20px;
        border: 2px solid #f42f54;
        border-radius: 40px;
      }
      &:last-child:hover {
        color: #fff;
        background-color: #f42f54;
      }
      &:hover {
        cursor: pointer;
        color: rgb(253, 77, 77);
        i {
          color: rgb(253, 77, 77);
        }
      }
      &:hover > ul {
        opacity: 1;
        visibility: visible;
      }
      .dropdown {
        display: flex;
        flex-wrap: wrap;
        position: absolute;
        left: calc(50% - 250px);
        width: 500px;
        height: 300px;
        padding: 2rem;
        background-color: rgba(32, 32, 32, 0.7);
        border-radius: 1rem;
        transition: all 0.3s;
        opacity: 0;
        visibility: hidden;
        li {
          flex-basis: 30%;
          text-align: left;
          color: #fff;
          &:hover {
            color: rgb(253, 77, 77);
          }
        }
      }
    }
  }
  i {
    display: inline-block;
    color: white;
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 480px) {
  .logo {
    img {
      max-width: 70% !important;
      max-height: 70% !important;
    }
  }
}
@media screen and (max-width: 991px) {
  .dropdown {
    left: 0 !important;
  }
}
@media screen and (max-width: 992px) {
  .menu {
    margin-top: 10px;
    text-align: center !important;
  }
}
@media screen and (min-width: 1200px) {
  .menu {
    .ele {
      margin-left: 3rem !important;
    }
  }
}
</style>
