<template>
  <div id="faq" class="container-fluid p-3 px-lg-5 py-lg-4">
    <div
      class="faq__name row wow fadeInUp"
      data-wow-duration="1s"
      data-wow-delay="0.1s"
    >
      <div class="col-12 text-center">
        <h2>faqs</h2>
        <span>
          Below weve provided a bit of ICO, ICO Token, cryptocurrencies, and
          few others. If you have any other questions, please get in touch.
        </span>
      </div>
    </div>
    <div class="faq__content row">
      <div
        class="faq__menu col-12 text-center wow fadeInUp"
        data-wow-duration="1s"
        data-wow-delay="0.3s"
      >
        <button
          v-for="(text, index) in menu"
          :key="index"
          @click="setActiveNumber(index)"
          v-bind:class="[activeNumber == index ? 'isActive' : '']"
        >
          {{ text }}
        </button>
      </div>
      <div
        class="faq__text col-12 wow fadeInUp"
        data-wow-duration="1s"
        data-wow-delay="0.6s"
      >
        <div class="row">
          <div
            class="faq__qna col-11 col-md-8 mx-auto"
            v-for="(question, index) in questions"
            :key="index"
          >
            <span
              class="question"
              @click="showAnswer(index)"
              v-bind:class="[activeAnswer == index ? 'activeAnswer' : '']"
            >
              {{ question }}
            </span>
            <transition name="show">
              <span class="answer" v-show="answerNumber == index">
                {{ answer }}
              </span>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menu: ["Garenal Questions", "ICO Questions", "Tokens Sales", "Investors"],
      activeNumber: 0,
      answerNumber: null,
      activeAnswer: null,
      questions: null,
      allQuestions: [
        [
          "What is ICO Crypto?",
          "What cryptocurrencies can I use to purchase?",
          "How can I participate in the ICO Token sale?",
          "How do I benefit from the ICO Token?",
        ],
        [
          "What is a reasonable valuation of the market?",
          "What does the competitive landscape look like?",
          "Does the team have a proof of concept (POC) or minimum viable product (MVP)?",
          "Does it appear that the main reason for launching the project is to provide ICO investors with returns?",
        ],
        [
          "Is the value of the token linked to an ICOs value proposition?",
          "Do the tokenomics make sense and are they likely to yield a return on the investment?",
          "Is using blockchain technology necessary and advantageous?",
          "Are the ideas and the white paper original?",
        ],

        [
          "Do critical contingencies exist?",
          "Is the team credible, competent and accomplished?",
          "Is the project operationally sound?",
          "Does the team have a good marketing budget and media presence?",
        ],
      ],
      answer:
        "Once ICO period is launched, You can purchased Token with Etherum, Bitcoin or Litecoin. You can also tempor incididunt ut labore et dolore magna aliqua sed do eiusmod eaque ipsa.",
    };
  },
  methods: {
    showAnswer(index) {
      if (this.answerNumber == index) {
        this.answerNumber = null;
        this.activeAnswer = null;
      } else {
        this.answerNumber = index;
        this.activeAnswer = index;
      }
    },
    setActiveNumber(num) {
      this.activeNumber = num;
      this.getQuestions();
    },
    getQuestions() {
      this.questions = this.allQuestions[this.activeNumber];
    },
  },
  mounted() {
    this.questions = this.allQuestions[0];
  },
};
</script>

<style lang="scss" scoped>
#faq {
  .faq__name {
    margin: 50px 0;
    h2 {
      color: #fff;
      font-family: "Roboto", sans-serif;
      text-transform: uppercase;
      margin-bottom: 20px;
    }
    span {
      color: #97afd5;
    }
  }
  .faq__content {
    .faq__menu {
      button {
        padding: 5px 15px;
        margin: 10px 10px;
        color: #fff;
        background-color: transparent;
        border: 1px solid #97afd5;
        border-radius: 5px;
        outline: none;
        transition: all 0.3s;
        &.isActive {
          background-color: #f42f54;
          border-color: #f42f54;
        }
        &:hover {
          background-color: #f42f54;
          border-color: #f42f54;
        }
      }
    }
    .faq__text {
      margin-top: 50px;
      .faq__qna {
        background-color: rgba(255, 255, 255, 0.05);
        padding: 20px 10px;
        margin: 10px 0;
        border-radius: 8px;
        cursor: pointer;
        &:hover > .question {
          transition: color 0.3s;
          color: #f42f54;
        }
        .question {
          display: block;
          color: #fff;
          font-weight: 550;
          letter-spacing: 1px;
          &.activeAnswer {
            color: #f42f54;
          }
        }
        .answer {
          display: block;
          color: #97afd5;
          margin-top: 10px;
        }
      }
    }
  }
}
.show-enter-active {
  transition: opacity 1s;
}
.show-leave-active {
  transition: opacity 0.5s;
}
.show-enter,
.show-leave-to {
  opacity: 0;
}
.show-enter-to,
.show-leave {
  opacity: 1;
}
</style>
