<template>
  <div id="register">
    <div class="logo">
      <img
        src="@/assets/logo-s2-white.png"
        alt="logo"
        v-on:click="backToHome()"
      />
    </div>
    <div class="register__form">
      <h3>Register</h3>
      <span id="not-empty">The field cannot be empty</span>
      <input
        id="username"
        type="text"
        placeholder="Email"
        v-on:keyup.enter="sendRegisterRequest()"
      />
      <span id="username-already-exists">The username already exists</span>
      <input
        id="password"
        type="password"
        placeholder="Password"
        v-on:keyup.enter="sendRegisterRequest()"
      />
      <input
        id="confirm-password"
        type="password"
        placeholder="Confirm Password"
        v-on:keyup.enter="sendRegisterRequest()"
      />
      <span id="confirmation-pwd-ii"
        >The confirmation password is incorrect</span
      >
      <button v-on:click="sendRegisterRequest()">Register Now</button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    backToHome() {
      location.replace("/");
    },
    sendRegisterRequest() {
      let username = document.getElementById("username").value;
      let password = document.getElementById("password").value;
      let confirm_password = document.getElementById("confirm-password").value;

      //check session exists
      if (this.$session.exists()) {
        // if not, then initialize
        this.$session.start();
      }
      // error messages
      let not_empty = document.getElementById("not-empty");
      let usernam_already_exists = document.getElementById(
        "username-already-exists"
      );
      let confirmation_pwd_ii = document.getElementById("confirmation-pwd-ii");

      //check field empty
      if (username === "" || password === "" || confirm_password === "") {
        not_empty.style.display = "block";
      } else {
        // if pass, hide empty error message
        not_empty.style.display = "none";
        // check if username exists or not
        if (!this.$session.has(username)) {
          usernam_already_exists.style.display = "none";
          // check 2 fields password are same
          if (password === confirm_password) {
            confirmation_pwd_ii.style.display = "none";
            this.$swal({
              title: "Register successfully!",
              text: "Now, please login",
              icon: "success",
            });
            this.$session.set(username, password);
          } else {
            // if pass, hide pwd error message
            confirmation_pwd_ii.style.display = "inline";
          }
        } else {
          // if pass, hide exists error message
          usernam_already_exists.style.display = "inline";
        }
      }
      document
        .querySelector(".swal2-confirm")
        .addEventListener("click", function() {
          location.href = "/login";
        });
    },
  },
};
</script>

<style lang="scss" scoped>
#register {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .logo {
    img {
      max-width: 100%;
      cursor: pointer;
    }
    margin-top: 20px;
    margin-bottom: 50px;
  }
  .register__form {
    width: 400px;
    max-width: 100vw;
    padding: 30px;
    border-radius: 10px;
    background-color: #fff;
    h3,
    input {
      display: block;
    }
    h3 {
      color: #333;
      margin-bottom: 20px;
      text-align: center;
      text-transform: uppercase;
    }
    input,
    button {
      width: 100%;
      padding: 10px 15px;
      margin-top: 20px;
      border: 1px solid #97afd5;
      border-radius: 10px;
      outline: none;
    }
    span {
      display: none;
      color: red;
      font-size: 14px;
      margin-left: 15px;
      &#not-empty {
        text-align: center;
      }
    }

    button {
      display: block;
      color: #fff;
      margin-top: 30px;
      background-color: rgb(71, 146, 71);
    }
  }
}
@media screen and (max-width: 480px) {
  .register__form {
    width: 300px !important;
  }
}
@media screen and (max-width: 359px) {
  .register__form {
    width: 280px !important;
  }
}
</style>
