<template>
  <transition name="show">
    <div id="mini-menu" class="d-md-none" v-if="isShow.value">
      <ul>
        <li>Home</li>
        <li>page</li>
        <li>Blog & Misc</li>
        <li>Elements</li>
        <li>Blocks</li>
        <li @click="forwardLoginForm()">
          {{ getUsername() }}
        </li>
      </ul>
      <span class="close" @click="close()"><i class="fas fa-times"></i></span>
    </div>
  </transition>
</template>

<script>
export default {
  props: ["isShow"],
  methods: {
    close() {
      this.isShow.value = !this.isShow.value;
    },
    forwardLoginForm() {
      location.href = "/login";
    },
    getUsername() {
      if (this.$session.has("isLogged")) {
        let result = this.$session.get("isLogged");
        if (result.length > 8) {
          return result.substring(0, 8) + "...";
        }
        return result;
      }
      return "Login";
    },
  },
};
</script>

<style lang="scss" scoped>
#mini-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: transparent;
  z-index: 10;
  ul {
    position: relative;
    float: right;
    display: flex;
    flex-wrap: wrap;
    width: 66.67%;
    height: 100%;
    padding: 50px;
    overflow: hidden;
    background-color: #fff;
    li {
      flex-basis: 100%;
      color: #333;
      margin: 20px 0;
      cursor: pointer;
      transition: all 0.3s;
      &:first-letter {
        text-transform: uppercase;
      }
      &:not(:last-child):hover {
        color: #f42f54;
      }
      &:last-child {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 35px;
        max-height: 45px;
        text-transform: uppercase;
        color: #fff;
        border: 1px solid #f42f54;
        border-radius: 40px;
        background-color: #f42f54;
        outline: none;
        &:hover {
          background-color: #fff;
          color: #f42f54;
        }
      }
    }
  }
  .close {
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 3px 15px;
    color: #fff;
    border: 1px solid #f42f54;
    border-radius: 5px;
    background-color: #f42f54;
    cursor: pointer;
    opacity: 1;
  }
}
.show-enter,
.show-leave-to {
  transform: translateX(100%);
}
.show-enter-to {
  transform: translateX(0);
}
.show-enter-active,
.show-leave-active {
  transition: all 0.5s;
}
</style>
